// Bootstrap Functions
@import "../../../node_modules/bootstrap/scss/functions";

// Theme Variables
@import "argon-design-system/variables";

// Bootstrap Core
@import "../../../node_modules/bootstrap/scss/bootstrap";

// Theme Core
@import "argon-design-system/theme";

// React Differences
@import "react/react-differences";

.slick-track {
  padding-bottom: 30px;
  .slick-slide {
    padding: 15px;
  }
}
.slick-arrow:before {
  color: #222ab9;
  font-size: 26px;
}


.with-shadow {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    width: 300px;
    height: 50px;
    top: 160px;
    margin: auto;
    left: 0px;
    right: 0px;
    background: #bfbfbf;
    z-index: -1;
    filter: blur(30px);
  }
}